import { TitleCasePipe } from '@angular/common';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';

@Component({
  selector: 'app-pay-slip',
  templateUrl: './pay-slip.component.html',
  styleUrls: ['./pay-slip.component.scss']
})
export class PaySlipComponent implements OnInit {

  public addpayslipForm: any;

  constructor(private cdr: ChangeDetectorRef,private formBuilder: FormBuilder,private toastService: ToastNotificationService,
    private titleCasePipe: TitleCasePipe,) { }

  ngOnInit(): void {
    
    this.createpayslipForm();
  }

  createpayslipForm(){
    this.addpayslipForm = this.formBuilder.group({
      year : [""],
      month: [""],   
    });
  }

}
