import { Component, OnInit } from '@angular/core';
import { AddbankemployeeComponent } from './addbankemployee/addbankemployee.component';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';

@Component({
  selector: 'app-bank-account',
  templateUrl: './bank-account.component.html',
  styleUrls: ['./bank-account.component.scss']
})
export class BankAccountComponent implements OnInit {

  public bankDetails = {
    "accountholdername": "ashleah",
    "accountnumber":"12345678910",
    "bankname":"Bank of India",
    "city": "Nagpur",
    "ifsccode": "IFSC1234",
  }
  public addressDetails = {
    "address": "pune",
    "country": "India",
    "State": "Maharashtra",
    "city": "Pune",
    "pincode": "411036"
  }
  constructor(public dialog: MatDialog,) { }

  ngOnInit(): void {
  }

  openADDBANK() {
    const dialogRef: MatDialogRef<AddbankemployeeComponent> = this.dialog.open(AddbankemployeeComponent, {
      width: '700px',
      height: 'auto',
      data: {  },
      panelClass: 'custom-dialog-account-class',
      disableClose: true,
      position: {
        right: '0',
      },
    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });

  }

}
