import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { PaySlipComponent } from './pay-slip/pay-slip.component';
import { SalaryStructureComponent } from './salary-structure/salary-structure.component';
import { BankAccountComponent } from './bank-account/bank-account.component';
import { MainPayrollComponent } from './main-payroll/main-payroll.component';

const routes: Routes = [
  {
    path:'',
    component:MainPayrollComponent,
    children: [
      {
        path:'',
        redirectTo: 'emp-payslip',
        pathMatch: 'full'
      },
      {
        path:'emp-payslip',
        component:PaySlipComponent,
      },
      {
        path:'emp-salary',
        component:SalaryStructureComponent,
      },
      {
        path:'emp-account',
        component:BankAccountComponent,
      },
    ],
    pathMatch: 'prefix',
    data: {
      routerLinkActiveOptions: { exact: true, pathMatch: 'full' }
    }
  },
]


@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class EmployeepayrollRoutingModule { }
